.image-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .image-modal {
    position: relative;
    outline: none;
    width: 80%;
    max-width: 800px;
    max-height: 80vh;
  }
  
  .image-modal-img {
    width: 100%;
    height: auto;
  }